


[class*="Panel-"] {
  display: flex;
}

.Panel-1-Nav {
  flex-direction: column;
  height: 100vh;
  background-color: rgb(125, 224, 136);
  margin: 1%;
  border-radius: 8px;
  width: 30vw;
  font-size: 30px;
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-1-Nav > *:first-child {
  padding: 5% 10% 5% 10%;
}

.Panel-2-Nav-User {
  margin: 5%;
  width: 50%;
  border-radius: 8px;
  background-color: rgb(44, 197, 46);
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-2-Nav-Tile {
  margin: 5%;
  width: 100%;
  padding: 5%;
  border-radius: 8px;
  background-color: rgb(44, 197, 46);
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-0 {
  background-color: rgb(165, 224, 177);
  height: 100%;
  width: 100%;
}

ul {
  list-style-type: none;  /* Removes bullet points */
  padding-left: 0;         /* Removes the default padding */
}

.Panel-1-Nav > a {
	color: #18272F;
  position: relative;
  text-decoration: none;
}

.Panel-1-Nav > a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #18272F;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.Panel-1-Nav > a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.emphasize-dark {
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.emphasize-light {
  box-shadow: 0 0 0 10px rgba(255,255,255,.25);
}

.emphasize-inset {
  box-shadow: inset 0 0 7px 4px rgba(255,255,255,.5);
}

.emphasize-border {
  box-shadow: inset 0 0 0 7px rgba(255,255,255,.5);
}